import React, { Component } from "react";
import { Link } from "react-router-dom";
import { forgotPassword } from '../../../actions/loginActions'
class ForgotPassword extends Component {
    constructor(props) {
        super(props)
    }
    state = {
        accountEmailId: null,
        emailError: null,
        emailErrorMessage: null,
        success: null
    }
    onEmailTextChange = event => {
        this.setState({ accountEmailId: event.target.value, emailError: null, emailErrorMessage: null })
    }
    onClickToForgot = event => {
        const { accountEmailId } = this.state;
        const { activeProduct } = this.props
        var self = this;
        if (accountEmailId && String(accountEmailId).length > 0) {
            forgotPassword({ emailId: accountEmailId, activeProduct }).then((response) => {
                self.setState({ success: true, emailErrorMessage: null })
            }).catch((ex) => {
                const { data } = ex.response

                self.setState({ emailError: true, emailErrorMessage: `Couldn't find your account with this e-mail` })
            })
        }
        else {

            this.setState({ emailError: true })
        }

    }
    render() {
        const { prefix } = this.props
        const { accountEmailId, emailError, success, emailErrorMessage } = this.state
        return <>
            <div className="loginFormSec registerFormSec formControlMin">
                <div className="loginBlock">
                    <div className="loginInnSec">
                        <div className="loginHead">
                            <h2>Forgot Password</h2>
                            <p>For existing user</p>
                        </div>

                        <p className="text-center"><strong>Enter your email address below to reset your password.</strong></p>

                        <div className="spacer10px"></div>
                        {success ? <div id="flashFlushMessage" className="note note-success">
                            <p className="mb-0">
                                Success! Please check your e-mail to reset your password.	</p>
                        </div> : null}
                        <div className="spacer15px"></div>

                        <div id="AccountForgotpasswordForm" >
                            <div className="form-group labelPosition">
                                <label htmlFor="AccountEmailId">Email Id</label>
                                <input name="data[Account][emailId]" className={`form-control placeholder-no-fix ${emailError ? `form-error` : ``}`} maxLength="255" type="email" id="AccountEmailId" value={accountEmailId} onChange={this.onEmailTextChange} />
                                {emailError ? <div className="error-message">{emailErrorMessage && emailErrorMessage.length > 0 ? emailErrorMessage : `Email is required.`}</div> : null}
                            </div>
                            <div className="form-group">
                                <button className="btn btn-primary w-100" data-style="expand-left" onClick={this.onClickToForgot}><span className="ladda-label changeText">Submit</span></button>
                            </div>
                            <div className="form-group">
                                <p>Take me back to <Link to={`${prefix}accounts/login`} className="btnSignUp">Login</Link></p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    }
}

export default ForgotPassword